<script>
import Header from "../../components/global/header/HeaderArea.vue"
import CitySelecter from "./components/CitySelecter.vue"
import { Toast } from '../../components/global/swal.js'
import { ref, onMounted, watch } from 'vue'
import { apiHospital, apiHospitalAllCity } from "@/api/api"

export default {
  setup() {
    let hospitalData = ref([])
    let cityIdx = ref(0)
    let areaIdx = ref(0)

    const updateCityIdx = (val) => {
      cityIdx.value = val.value
    }
    const updateAreaIdx = (val) => {
      areaIdx.value = val.value
    }

    watch(() => cityIdx.value, async () => {
      hospitalData.value = await apiHospitalAllCity({ cityId: cityIdx.value })
      hospitalData.value = hospitalData.value.data
    })
    watch(() => areaIdx.value, async () => {
      hospitalData.value = await apiHospital({ cityId: cityIdx.value, areaId: areaIdx.value })
      if (hospitalData.value.data.length == 0) {
        Toast("該區域無資料")
        hospitalData.value = []
      } else {
        hospitalData.value = hospitalData.value.data
      }
    })

    return { hospitalData, updateCityIdx, updateAreaIdx }
  },
  components: {
    Header, CitySelecter
  },
};
</script>

<template>
  <Header title="醫院" right="map" path="/map/hospitalMap" />
  <div class="hospital">
    <section class="c-main">
      <CitySelecter @city-idx="updateCityIdx" @area-idx="updateAreaIdx" />
      <div class="resource-container">
        <div class="card" v-for="item in hospitalData" :key="item.properties?.id">
          <div class="card-header">
            <a :href=item.properties?.website class="card-link">
              <i class="far fa-link"></i>
            </a>
          </div>
          <div class="card-body">
            <a :href=item.properties?.website class="card-link">
              <div class="card-title">{{ item.properties?.name }}</div>
              <div class="card-text">
                {{ item.properties?.city.name }}
                {{ item.properties?.area.name }}
                {{ item.properties?.address }}
              </div>
            </a>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>


<style lang="scss" scoped>
@import "@/assets/custom/config";

.hospital {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}
</style>